___CSS_LOADER_EXPORT___.push([module.id, `.ai-style-wand-modal {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.ai-style-wand-modal-header h3 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #333;
}

.ai-style-wand-modal-body {
  margin-bottom: 20px;
}

.ai-style-textarea {
  width: 100%;
  min-height: 100px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  resize: vertical;
}

.ai-style-suggestions {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 10px;
}

.ai-style-tag {
  display: inline-block;
  padding: 5px 10px;
  background-color: #f0f0f0;
  border-radius: 20px;
  font-size: 12px;
  color: #333;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.ai-style-tag:hover {
  background-color: #e0e0e0;
}

.ai-style-wand-modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.ai-style-apply-btn, .ai-style-cancel-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.ai-style-apply-btn {
  background-color: #4CAF50;
  color: white;
}

.ai-style-apply-btn:hover {
  background-color: #45a049;
}

.ai-style-cancel-btn {
  background-color: #f5f5f5;
  color: #333;
}

.ai-style-cancel-btn:hover {
  background-color: #e0e0e0;
} `, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,QAAQ;EACR,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,iBAAiB;EACjB,yBAAyB;EACzB,mBAAmB;EACnB,eAAe;EACf,WAAW;EACX,eAAe;EACf,sCAAsC;AACxC;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,SAAS;AACX;;AAEA;EACE,iBAAiB;EACjB,YAAY;EACZ,kBAAkB;EAClB,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,yBAAyB;EACzB,YAAY;AACd;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;EACzB,WAAW;AACb;;AAEA;EACE,yBAAyB;AAC3B","sourcesContent":[".ai-style-wand-modal {\r\n  padding: 20px;\r\n  font-family: Arial, sans-serif;\r\n}\r\n\r\n.ai-style-wand-modal-header h3 {\r\n  margin-top: 0;\r\n  margin-bottom: 20px;\r\n  color: #333;\r\n}\r\n\r\n.ai-style-wand-modal-body {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.ai-style-textarea {\r\n  width: 100%;\r\n  min-height: 100px;\r\n  padding: 10px;\r\n  border: 1px solid #ddd;\r\n  border-radius: 4px;\r\n  font-size: 14px;\r\n  resize: vertical;\r\n}\r\n\r\n.ai-style-suggestions {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 8px;\r\n  margin-top: 10px;\r\n}\r\n\r\n.ai-style-tag {\r\n  display: inline-block;\r\n  padding: 5px 10px;\r\n  background-color: #f0f0f0;\r\n  border-radius: 20px;\r\n  font-size: 12px;\r\n  color: #333;\r\n  cursor: pointer;\r\n  transition: background-color 0.2s ease;\r\n}\r\n\r\n.ai-style-tag:hover {\r\n  background-color: #e0e0e0;\r\n}\r\n\r\n.ai-style-wand-modal-footer {\r\n  display: flex;\r\n  justify-content: flex-end;\r\n  gap: 10px;\r\n}\r\n\r\n.ai-style-apply-btn, .ai-style-cancel-btn {\r\n  padding: 8px 16px;\r\n  border: none;\r\n  border-radius: 4px;\r\n  cursor: pointer;\r\n  font-size: 14px;\r\n}\r\n\r\n.ai-style-apply-btn {\r\n  background-color: #4CAF50;\r\n  color: white;\r\n}\r\n\r\n.ai-style-apply-btn:hover {\r\n  backg